











import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { Movel } from "@/api";

@Component
export default class extends Vue {
  @Prop({
    type: Object,
    required: true,
  })
  public movel!: Movel;

  @Prop({
    type: Number,
    required: true,
  })
  public id!: number;
}
